*{
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;

}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

ul,
ol,
li {
  list-style: none;
  margin: 0;
  padding: 0;
}

.list {
  list-style: none;
  margin: 0;
  padding: 0;
}

.country-info .name {
  margin-left: 20px;
  font-size: 40px;
  font-weight: 600;

}

.country-list .name{
margin-left: 10px;
}

.country__list-item :hover{
  font-weight: 400;

}

.title{
  font-weight: 600;
}

.title_info{
  margin-left: 10px;
}